export default {
	data() {
		return {
			many: '0',
			user_id: [],
			flag: false,
            greed:'去阅读',
            cancl:'取消',
            lerst:'若不阅读和同意协议,无法使用此功能',
			selectilall: false,
            deoor:false,
            stus:'',
            shade:false,
            shade1:false,
			machine_id: '',
			lo: false,
			arr: [],
			isSelected: false,
			checkbox: [
				{
					value: 'A',
					checked: false
				}
			]
		};
	},

	onShow(options) {
		var that = this;
		uni.request({
			url: this.url + 'mainmachine/',
			method: 'GET',
			header: {
				Authorization: 'JWT' + ' ' + this.global_.token
			},
			success(res) {
                
				console.log(res);
				that.user_id = res.data.data;
				if (res.statusCode == 205) {
					that.flag = true;
				}
                that.many = res.data.data.length;
                console.log(that.many)
                if (that.many == 0) {
                	that.flag = true;
                } else {
                	that.flag = false;
                }
			}
		});
	},
    onHide() {
          this.shade=false
        },
	methods: {
		pay: function() {
			uni.navigateTo({
				url: '../../mill/pay/pay'
			});
		},
		sale: function() {
			uni.navigateTo({
				url: '../../mill/sale/sale'
			});
		},
		// 点击选中矿机
		CheckboxChange(e) {
			var that = this;
			that.arr = [];
			for (let i = 0; i < that.user_id.length; i++) {
				that.user_id.checked = false;
			}
			var items = that.user_id;
			var values = e.detail.value;
			for (var i = 0, lenI = items.length; i < lenI; ++i) {
				items.checked = true;
				for (var j = 0, lenJ = values.length; j < lenJ; ++j) {
					if (items[i].number == values[j]) {
						items.checked = true;
						that.arr.push(items[i].machine_id);
						break;
					}
				}
			}
		},
        
		btn2: function() {
			var that = this;
			var a = that.arr.join(',');
			console.log(a);
			uni.request({
				url: this.url + 'buildorders/',
				method: 'GET',
				header: {
					Authorization: 'JWT' + ' ' + this.global_.token
				},
				data: {
					machine_id_list: a
				},
				success(res) {

					console.log(res)
                    console.log(that.shade)
                    if(res.statusCode == 410) {
                        that.shade = true
                        that.stus = res.statusCode
                    } else
                        if (that.arr.length == 0) {
                    	uni.showToast({
                    		title: '请选择矿机',
                    		icon: 'none'
                    	});
                        return false
                    }

					if (res.statusCode == 401) {
                        that.stus = res.statusCode
                        that.lerst = '未进行实名认证'
                        that.greed = '去验证'
                        that.shade = true
						return false
					}
					if (res.statusCode == 302) {
                        that.stus = res.statusCode
                        that.shade1 = true
						return false
					}
					if (res.statusCode == 400) {
                        that.stus = res.statusCode
                        that.lerst = '未设置交易密码'
                        that.greed = '去设置'
                        that.shade = true
						return false
					}
					if (res.statusCode == 200) {
                        var asr = JSON.stringify(res.data.data);
						uni.navigateTo({
							url: '../../mill/sell/sell?tar=' + asr
						});
					}
				}
			});
		},
        sure:function(){
            if(this.stus == 400) {
             	uni.switchTab({
                	url: '../my/my'
                });
            }
            if(this.stus == 410) {
                uni.navigateTo({
                    url:'../agreement/agreement'
                })
            }
            if(this.stus == 401) {
                uni.navigateTo({
                	url: '../../my/identity/identity'
                });
            }
            if(this.stus == 302) {
                this.shade1 = false
            }
        },
        cancel:function(){
           this.shade = false
        },
	}
};
